@import url("https://fonts.googleapis.com/css2?family=Lemonada&display=swap");

a,
a:hover,
a:visited {
  text-decoration: inherit;
}

html {
  font-size: 14px;
}

body {
  position: relative;
  overflow-x: hidden;
}

.vCenter {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.myLinks {
  display: flex;
  align-items: center;
  justify-content: center;
}

.myLinks img {
  height: 2.5rem;
  width: auto;
}

.linkedInText {
  color: white;
  font-weight: bold;
  margin-right: 0.5rem;
  font-size: 1.5rem;
}

.mainContainer {
  padding: 0 !important;
  margin: 0 !important;
  position: relative;
  overflow-x: hidden;
  overflow-y: hidden;
  min-height: 100vh;
  background-color: black;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#bkgVideo {
  width: 100vw;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
}

#mainHeader * {
  display: block;
  border: 1px solid white;
  border-radius: 0.5rem;
  background-color: rgba(255, 255, 255, 0.6);
  color: #0c4b8c;
  padding: 0.5rem 1rem;
}

#mainHeader p {
  flex-grow: 1;
}

.jt {
  background-color: rgba(255, 255, 255, 0.5);
  margin: 1.5rem 5rem;
  z-index: 1;
}

.jt p {
  color: azure;
  font-size: 1.25rem;
  margin-top: 1rem;
  text-indent: 2.5rem;
  overflow-y: auto;
}

.techList {
  background-color: rgba(255, 255, 255, 0.6);
  border: 2px solid white;
  border-radius: 0.5rem;
  padding: 0.5rem;
  height: 20rem;
  overflow-y: auto;
}

.techList ul {
  columns: 2;
  -webkit-columns: 2;
  -moz-columns: 2;
  height: auto;
}

.jtCol {
  display: flex;
  flex-direction: column;
  padding: 1rem;
  height: 20rem;
}

.portfolioImg {
  margin: 1rem 0.5rem;
  border-radius: 0.5rem;
  border: 3px solid #0c4b8c;
  padding: 1rem;
  background-color: black;
  position: relative;
  overflow: hidden;
}

.portfolioImg:hover .portfolioInfo {
  bottom: 0;
  transition: 0.5s;
}

.portfolioImg:hover {
  border-color: white;
  transition: 2.5s;
}

.portfolioImg img {
  width: 100%;
}

.portrait .portfolioInfo {
  border-bottom-left-radius: 50%;
  border-bottom-right-radius: 50%;
}

.portfolioInfo {
  position: absolute;
  bottom: 100%;
  left: 0;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.8);
  transition: 0.5s;
  text-align: center;
  color: white;
  overflow-y: auto;
  padding-top: .5rem;
}

.portfolioInfo h1 {
  font-size: 1.3rem;
}

.portfolioInfo p {
  font-size: 1rem;
  padding: 1rem;
  text-align: justify;
  text-indent: 1rem;
}

.portfolioInfo img {
  width: 30%;
}


svg {
  color: white;
  background-color: white;
  border-radius: 1rem;
}

.portrait {
  border-radius: 50%;
  border: 5px solid #0c4b8c;
  overflow: hidden;
  width: 15rem;
  height: 15rem;
  background-size: cover;
  background-repeat: no-repeat;
  align-self: end;
  position: relative;
  transition: 0.5s;
  margin: auto;
}

.portrait:hover .portfolioInfo {
  bottom: 0;
  transition: 0.5s;
}

.portrait:hover {
  border-color: white;
  transition: 2.5s;
}

.footer {
  background-color: rgba(0, 0, 0, 0.99);
  height: 5rem;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  text-align: center;
  width: 100%;
  position: relative;
  bottom: 0;
}

.portfolioContainer {
  background-color: rgba(9, 8, 40, 0.8);
  z-index: 1;
  flex-grow: .5;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.portfolioCol {
  display: flex;
  justify-content: space-evenly;
}

@media only screen and (max-width: 1000px) {
  .jt {
    padding-bottom: 0;
  }

  .jtCol1 {
    order: 1;
    width: 100%;
  }

  .jtCol {
    height: auto;
    /* margin: 0; */
    padding-left: 0;
    padding-right: 0;
  }

  .jt {
    background-color: unset;
    margin: 0;
  }

  body {
    background-color: #0c4b8c;
  }

  .portfolioContainer {
    min-height: unset;
  }
}

@media only screen and (max-width: 600px) {
  .jumbotron {
    margin: 0 1rem;
    background-color: unset;
  }

  .jtCol {
    height: auto;
  }

  .jtCol p {
    margin: 0.25rem;
    padding: 0.25rem;
    font-size: 0.85rem;
  }

  .jtCol1 {
    order: 0;
  }

  .portfolioImg {
    margin: 0.5rem auto;
    width: 100%;
    flex-wrap: wrap;
    cursor: pointer;
  }

  .portfolioImg p {
    display: none;
  }

  .portfolioImg h1 {
    font-size: 2rem;
    font-weight: bold;
  }

  .portfolioInfo img {
    width: 30%;
  }

  .portfolioCol {
    display: block;
  }

  .techList {
    height: auto;
  }

  .techList h3 {
    text-align: center;
  }

  .portfolioContainer {
    background-color: unset;
  }
}